import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ScreenOrientation } from '@ionic-native/screen-orientation';

/**
 * Generated class for the CompaniasPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-companias',
  templateUrl: 'companias.html',
})
export class CompaniasPage {
  public backgroundImage = 'assets/img/fondo.jpg'; 
  public companiaitems: Array<Object>;
  public items:  any;
  
  constructor(public navCtrl: NavController, public navParams: NavParams,public screenOrientation: ScreenOrientation) {
    this.items = navParams.data;
    //screenOrientation.lock(screenOrientation.ORIENTATIONS.PORTRAIT);
  }

   goToprincipal(id: number){
   

    this.companiaitems = this.items.find(companias => companias.id == id); 
    //console.log( this.companiaitems); 
    let itemsm: any = [];
    itemsm.push(this.companiaitems);
    //console.log( this.itemsm);
    this.navCtrl.push('DetallecompaniasPage', itemsm);
  }

}



